<template>
      
      <div class="box">  
        <h3>Not G端ncelle</h3>
      <form @submit.prevent="onUpdateForm">
 
      <textarea style="width: 100%;"
      rows="4"
       class="form-control"
      placeholder="Not"
      v-model="not.not_icerik"
      required
       ></textarea>
 
          <input
            type="time"
            class="form-control"
            v-model="not.not_saati"
            required
          />
 

          <button class="btn btn-update">G端ncelle</button>
 
      </form>
    </div>

</template>

<script>
import { db } from "../firebaseDb";
export default {
  data() {
    return {
      not: {},
    };
  },
  created() {
    let dbRef = db.collection("notlar").doc(this.$route.params.id);
    dbRef
      .get()
      .then((doc) => {
        this.not = doc.data();
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    onUpdateForm(event) {
      event.preventDefault();
      db.collection("notlar")
        .doc(this.$route.params.id)
        .update(this.not)
        .then(() => {
          console.log("User successfully updated!");
          this.$router.push("/notlist");
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>